const routes = [
  {
    path: 'merchant-tasks',
    name: '【J东】 商家预付',
    component: () => import('@/pages/jd/merchantTasks'),
    meta: {
      title: '【J东】 商家预付',
    },
  },
  {
    path: 'bee-tasks',
    name: '【J东】 买手垫付',
    component: () => import('@/pages/jd/beeTasks'),
    meta: {
      title: '【J东】 买手垫付',
    },
  },
];

routes.forEach((route) => {
  if (!route.path.startsWith('/')) {
    // eslint-disable-next-line
    route.path = '/jd/' + route.path;
  }
  // eslint-disable-next-line
  route.meta.isTop = true;
});

export default routes;



