import tbRoutes from './tb';
import jdRoutes from './jd';
import pddRoutes from './pdd';
import orderRoutes from './order';

const routes = [
  {
    path: '/tb',
    name: '淘',
    icon: 'fa fa-gift',
    redirect: '/tb/merchant-tasks',
    component: () => import('@/pages/tb/layout'),
    meta: {
      title: '淘',
    },
    children: tbRoutes,
  },
  {
    path: '/jd',
    name: 'J东',
    icon: 'fa fa-shopping-bag',
    redirect: '/jd/merchant-tasks',
    component: () => import('@/pages/jd/layout'),
    meta: {
      title: 'J东',
    },
    children: jdRoutes,
  },
  {
    path: '/pdd',
    name: '拼',
    icon: 'fa fa-shopping-cart',
    redirect: '/pdd/merchant-tasks',
    component: () => import('@/pages/pdd/layout'),
    meta: {
      title: '拼',
    },
    children: pddRoutes,
  },
  {
    path: '/order',
    name: '已接任务',
    icon: 'fa fa-tasks',
    redirect: '/order/uncompleted',
    component: () => import('@/pages/order/layout'),
    meta: {
      title: '已接任务',
    },
    children: orderRoutes,
  },
  {
    path: '/my',
    name: '个人中心',
    icon: 'fa fa-user',
    component: () => import('@/pages/my/self'),
    meta: {
      title: '个人中心',
    },
  },
];

routes.forEach((route) => {
  // eslint-disable-next-line
  route.meta.isTop = true;
});

export default routes;



