const routes = [
  {
    path: 'merchant-tasks',
    name: '【淘】 商家预付',
    component: () => import('@/pages/tb/merchantTasks'),
    meta: {
      title: '【淘】 商家预付',
    },
  },
  {
    path: 'bee-tasks',
    name: '【淘】 买手垫付',
    component: () => import('@/pages/tb/beeTasks'),
    meta: {
      title: '【淘】 买手垫付',
    },
  },
  {
    path: 'explore',
    name: '【淘】 浏览任务',
    component: () => import('@/pages/tb/explore'),
    meta: {
      title: '【淘】 浏览任务',
    },
  },
];

routes.forEach((route) => {
  if (!route.path.startsWith('/')) {
    // eslint-disable-next-line
    route.path = '/tb/' + route.path;
  }
  // eslint-disable-next-line
  route.meta.isTop = true;
});

export default routes;



