<template>
  <div class="backHead" v-if="Status" @click="backHead"></div>
</template>

<script>
import {
  showBack,
} from '@/utils/mixin';

export default {
  name: 'BackHead',
  data() {
    return {
      Status: false,
    };
  },
  props: {
    backRouters: {
      type: String,
      default: '/',
    },
  },
  methods: {
    backHead() {
      const times = setInterval(() => {
        const top = document.body.scrollTop || document.documentElement.scrollTop;
        if (top <= 0) {
          clearInterval(times);
        }
        document.body.scrollTop = top - (top / 50);
        document.documentElement.scrollTop = top - (top / 50);
      }, 1);

      document.addEventListener('touchstart', () => {
        clearInterval(times);
      }, {
        passive: true,
      });

      document.addEventListener('touchmove', () => {
        clearInterval(times);
      }, {
        passive: true,
      });
    },
  },
  mounted() {
    showBack((status) => {
      this.Status = status;
    });
  },
};
</script>

<style lang='scss' scoped>
@import '@/assets/common/css/mixin.scss';
/* eslint-disable max-len */

.backHead {
  position: fixed;
  bottom: 62px;
  right: 9px;
  z-index: 9999999999999;
  @include wh(38px, 38px);
  @include bg("data:image/png;base64,"); // eslint-disable-line
}

</style>



