const routes = [
  {
    path: 'readme',
    name: '规则和积分制度',
    icon: 'fab fa-readme',
    iconColor: '#8bc34a',
    component: () => import('@/pages/readme/index'),
    meta: {
      title: '规则和积分制度',
    },
  },
  {
    path: 'credit',
    name: '等级与积分',
    icon: 'fas fa-certificate',
    iconColor: '#ef6c00',
    description: '等级越高，接单越多',
    component: () => import('@/pages/my/credit'),
    meta: {
      title: '等级与积分',
      description: '等级越高，接单越多',
    },
  },
  {
    path: 'profile',
    name: '完善资料',
    icon: 'fa fa-id-card',
    iconColor: '#f06292',
    description: '完善资料，奖励5金币',
    component: () => import('@/pages/my/profile'),
    meta: {
      title: '完善资料',
      description: '完善资料，奖励5金币',
    },
  },
  {
    path: 'jd/add',
    name: '添加东东号',
    icon: 'fas fa-plus-circle',
    iconColor: '#039be5',
    component: () => import('@/pages/jd/add'),
    meta: {
      title: '添加东东号',
    },
  },
  {
    path: 'money',
    name: '我的金币',
    icon: 'fas fa-dollar-sign',
    iconColor: '#fb8c00',
    component: () => import('@/pages/my/money'),
    meta: {
      title: '我的金币',
    },
  },
  {
    path: 'balance/cash',
    name: '我要提现',
    icon: 'fas fa-money-bill-alt',
    iconColor: '#f06292',
    component: () => import('@/pages/balance/cash'),
    meta: {
      title: '我要提现',
    },
  },
  {
    path: 'balance/password',
    name: '提现密码设置',
    icon: 'fas fa-key',
    iconColor: '#4fc3f7',
    component: () => import('@/pages/balance/password'),
    meta: {
      title: '提现密码设置',
    },
  },
  {
    path: 'students',
    name: '我的徒弟',
    icon: 'fas fa-users',
    iconColor: '#ffe082',
    description: '发展徒弟获提成',
    component: () => import('@/pages/students/index'),
    meta: {
      title: '我的徒弟',
    },
  },
  {
    path: 'password',
    name: '修改密码',
    icon: 'fas fa-unlock-alt',
    iconColor: '#9e9e9e',
    component: () => import('@/pages/my/password'),
    meta: {
      title: '修改密码',
    },
  },
  {
    path: 'logout',
    name: '退出登录',
    icon: 'fas fa-power-off',
    iconColor: '#00c853',
    component: () => import('@/pages/login/logout'),
    meta: {
      title: '退出登录',
    },
  },
];

const backTo = '/my';
routes.forEach((route) => {
  if (!route.path.startsWith('/')) {
    // eslint-disable-next-line
    route.path = '/my/' + route.path;
  }
  // eslint-disable-next-line
  route.meta.backTo = backTo;
});

export default routes;



