const routes = [
  {
    path: 'merchant-tasks',
    name: '【拼】 商家预付',
    component: () => import('@/pages/pdd/merchantTasks'),
    meta: {
      title: '【拼】 商家预付',
    },
  },
  {
    path: 'bee-tasks',
    name: '【拼】 买手垫付',
    component: () => import('@/pages/pdd/beeTasks'),
    meta: {
      title: '【拼】 买手垫付',
    },
  },
];

routes.forEach((route) => {
  if (!route.path.startsWith('/')) {
    // eslint-disable-next-line
    route.path = '/pdd/' + route.path;
  }
  // eslint-disable-next-line
  route.meta.isTop = true;
});

export default routes;



