const routes = [
  {
    path: '/tb/help',
    name: '【淘】 任务须知',
    component: () => import('@/pages/tb/help'),
    meta: {
      title: '【淘】 任务须知',
      backTo: '/tb',
    },
  },
  {
    path: '/order/help',
    name: '投诉',
    component: () => import('@/pages/order/help'),
    meta: {
      title: '投诉',
      backTo: '/order',
    },
  },
];

export default routes;



