<template>
  <div class="tabbar-container">
    <mt-tabbar fixed>
      <mt-tab-item
        v-for="route in navRoutes"
        :key="route.path">
        <div class="tabbar-item">
          <router-link :to="route.path">
            <div :class="route.icon" class="tabbar-icon" />
            <span>{{ route.meta.title || route.name }}</span>
          </router-link>
        </div>
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  data() {
    return {
      popupVisible: false,
      popupRoutes: [],
    };
  },
  computed: {
    ...mapGetters([
      'navRoutes',
    ]),
    path() {
      return this.$route.path;
    },
  },
};
</script>

<style lang='scss' scoped>
.tabbar-container {
  text-align: center;
}

.tabbar-item {
  padding: 0 5px 5px;
}

.tabbar-icon {
  padding-bottom: 5px;
  display: block;
  font-size: 28px;
}

a:active, a:hover, a.router-link-active {
  color: red;
}
</style>



