import Vue from 'vue';
import Router from 'vue-router';
import Layout from '@/pages/layout/Layout';
import {
  navRoutes,
  myRoutes,
  helpRoutes,
  tbRoutes,
  jdRoutes,
  pddRoutes,
  orderRoutes,
} from './modules';

Vue.use(Router);

const routes = [
  {
    path: '',
    component: Layout,
    redirect: '/my',
    children: navRoutes.concat(myRoutes).concat(helpRoutes),
  },
];

const appRouter = {
  base: __dirname,
  routes,
};

export {
  navRoutes,
  myRoutes,
  helpRoutes,
  tbRoutes,
  jdRoutes,
  pddRoutes,
  orderRoutes,
};
export default new Router(appRouter);



