<template>
  <section class="app-main" :class="showHeader ? 'with-header' : ''">
    <div class="app-header" v-if="showHeader">
      <mt-cell>
        <div slot="icon" class="header-back">
          <router-link :to="$route.meta.backTo || ''">
            <i class="mintui mintui-back"></i>
          </router-link>
        </div>
        <div slot="title" class="header-title">
          {{ $route.meta.title  }}
        </div>
        <div class="header-home">
          <router-link to="/">
            <i class="fas fa-home"></i>
          </router-link>
        </div>
      </mt-cell>
    </div>

    <transition name="fade-transform" mode="out-in">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </transition>
  </section>
</template>

<script>
export default {
  name: 'AppMain',
  computed: {
    showHeader() {
      return !this.$route.meta.isTop;
    },
  },
};
</script>

<style lang="scss">
.app-main {
  padding-bottom: 60px;
  min-height: 100vh;

  &.with-header {
    padding-top: 50px;
  }
}

.app-header {
  top: 0;
  right: 0;
  left: 0;
  position: fixed;
  z-index: 1;

  background-color:#f44336;
  color: white;

  a {
    color: white;
  }

  .mint-cell-wrapper {
    background-color:#f44336;
  }

  .header-back {
    float: left;
  }

  .header-title {
    text-align: center;
  }
}
</style>




