const routes = [
  {
    path: '/order/uncompleted',
    name: '未完成',
    component: () => import('@/pages/order/uncompleted'),
    meta: {
      title: '未完成',
    },
  },
  {
    path: '/order/merchant-paid',
    name: '已返款',
    component: () => import('@/pages/order/merchantPaid'),
    meta: {
      title: '已返款',
    },
  },
  {
    path: '/order/wait-confirm-delivery',
    name: '催确认收货',
    component: () => import('@/pages/order/askConfirmDelivery'),
    meta: {
      title: '催确认收货',
    },
  },
  {
    path: '/order/all',
    name: '所有',
    component: () => import('@/pages/order/all'),
    meta: {
      title: '所有',
    },
  },
];

routes.forEach((route) => {
  if (!route.path.startsWith('/')) {
    // eslint-disable-next-line
    route.path = '/order/' + route.path;
  }
  // eslint-disable-next-line
  route.meta.isTop = true;
});

export default routes;



